<script>
import 'ele-calendar/dist/vue-calendar.css' 
import moment from 'moment'
import audio from '@/static/audio.mp3'
export default {
  name: 'App',
  data() {
    return{
      changeNum: {},
      countDown: {
        day: [],
        hour: [],
        minute: [],
        second: []
      },
      audioSrc: audio,
      isPlay: false,
      scrollId: 'app',
      scrollDirection: 'down'
    }
  },
  methods: {
    renderContent(h,parmas) {
      const loop = data =>{
        return (
          data.defvalue.value ? (<div><div>{data.defvalue.text}</div> 
          <span  >备选项</span>
          </div>) : <div>{data.defvalue.text}</div>
        )
      }
      return (
      <div  style="min-height:60px;">
        {loop(parmas)}
      </div>
      );
    },
    openCountDown() {
      setInterval(() => {
        let start = moment(new Date())
        let end = moment(new Date("2022/10/07"));//结束时间
        let diff = end.diff(start);//时间差
        const dayArray = moment.duration(diff).days().toString().split("")
        dayArray.length===1 && dayArray.unshift("0")
        const hourArray = moment.duration(diff).hours().toString().split("")
        hourArray.length===1 && hourArray.unshift("0")
        const minuteArray = moment.duration(diff).minutes().toString().split("")
        minuteArray.length===1 && minuteArray.unshift("0")
        const secondArray = moment.duration(diff).seconds().toString().split("")
        secondArray.length===1 && secondArray.unshift("0")
        const countDown = {
          day: dayArray,
          hour: hourArray,
          minute: minuteArray,
          second: secondArray 
        }
        const changeNum = {}
        const array = [ 'day', 'hour', 'minute', 'second' ]
        array.forEach(item=> {
          if(countDown[item][0]!==this.countDown[item][0]) {
            changeNum[item + 0] = true
          }
          if(countDown[item][1]!==this.countDown[item][1]) {
            changeNum[item + 1] = true
            
          }
        })
          this.changeNum = changeNum
        setTimeout(() => {
          this.changeNum = {}
        }, 400);
        this.countDown = countDown
      }, 1000);
    },
    autoplay() {
        const audio = document.getElementById('audio')
        const play= () => {
          audio.muted = false
          audio.play()
          document.removeEventListener('touchend', play, false)
          document.removeEventListener('click', play, false)
          this.isPlay = !audio.paused
          
        }
        document.addEventListener('WeixinJSBridgeReady', () => {
          // play()
        })
        document.addEventListener('touchend', play, false)
        document.addEventListener('click', play, false)
      
    },
    playAudio() {
      const audio = document.getElementById('audio')
      audio.muted = false
      if(audio.paused) {
        audio.play()
      } else {
        audio.pause()
      }
      this.isPlay = !audio.paused
    },
    autoScroll() {
      const scrollHeight = document.getElementById(this.scrollId).scrollHeight
      const clientHeight = document.getElementById(this.scrollId).clientHeight
      const scroll = scrollHeight - clientHeight
      console.log(scrollHeight,clientHeight )
      // 滚动长度为0
      if (scroll === 0) {
        return
      }
      // 触发滚动方法
      this.scrollFun()
      // 去除点击监听
      window.document.removeEventListener('click', this.pauseScroll)
      // 重设点击监听
      window.document.addEventListener('click', this.pauseScroll, false)
    },
    pauseScroll() {
        this.closeScroll = true
    },
    scrollFun() {
      const step =  ()=> {
      // 距离目标滚动距离
        const scrollHeight = document.getElementById(this.scrollId).scrollHeight
        const clientHeight = document.getElementById(this.scrollId).clientHeight
        const scroll = scrollHeight - clientHeight;
        const scrollTop = document.getElementById(this.scrollId).scrollTop
        const temp = scrollTop + 1.2
        // 目标滚动位置
        if (scroll <= temp) {
          document.getElementById(this.scrollId).scrollTo(0, scroll);
        } else {
          document.getElementById(this.scrollId).scrollTo(0, temp);
          if(!this.closeScroll) requestAnimationFrame(step);
        }
      }
      step();
    }
  },
  mounted() {
    const that = this
    this.autoScroll()
    this.autoplay()
    this.openCountDown()
    window.wx.ready(function () {   //需在用户可能点击分享按钮前就先调用
      console.log('----')
      window.wx.updateAppMessageShareData({ 
        title: '1231', // 分享标题
        desc: '12312', // 分享描述
        link: '', // 分享链接，该链接域名或路径必须与当前页面对应的公众号 JS 安全域名一致
        imgUrl: '', // 分享图标
        success: function () {
          // 设置成功
        }
      })
    });
    setTimeout(() => {
      var map = new window.BMapGL.Map('map'); // 创建Map实例
      var myGeo = new window.BMapGL.Geocoder();
      myGeo.getPoint('山东省淄博市高青县青城镇食香阁酒店', function(point){
        that.point = point
        if(point){
            map.centerAndZoom(point, 21);
            map.addOverlay(new  window.BMapGL.Marker(point, {title: '淄博市高青县G233（渔洋街）青城镇食香阁酒店'}))
        }
      })
      map.addEventListener('click',function(){
        window.open(`http://api.map.baidu.com/marker?location=${that.point.lat},${that.point.lng}&title=我的位置&content=高青县青城镇食香阁酒店&output=html&src=webapp.baidu.openAPIdemo  `)

            // map.openInfoWindow(window.BMap.InfoWindow,that.point)

      })
      map.enableScrollWheelZoom(true); // 开启鼠标滚轮缩放
    }, 100);
    
  }
}
</script>

